:root {
  --clr-primary: #asd;
  --clr-secondary: #asd;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
p {
  margin: 0;
}

body {
  /*font-family: var(primary);*/
  /*font-weight: ;*/
  font-size: 2rem;
  /*line-height: ;*/
  text-align: center;
  background-color: #282c34;
  color: white;
}

.App {
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

.Active {
  background-color: #33cb45;
  height: 60px;
  font-size: 1rem;
}

.button {
  min-width: 150px;
  height: 60px;
  font-size: 1rem;
}
.button :disabled,
button[disabled] {
  background-color: #b12946;
  color: white;
}

.row {
  display: grid;
  grid-auto-flow: row;
  padding-left: 1rem;
  padding-right: 1rem;
  /*justify-content: center;*/
  /*width: 400px;*/
  /*min-width: 300px;*/
  /*max-width: 200px;*/
  /*width: max(400px, 40%);*/
  /*border-color: #2196f3;*/
  /*border-style: solid;*/
  /*border-width: medium;*/
  /*border-radius: 10px;*/
}

@media only screen and (min-width: 500px) {
  .row {
    width: 30rem;
  }
  .Tattoo-logic {
    display: grid;
    justify-content: center;
  }
}

@media only screen and (min-width: 768px) {
  .row {
    grid-auto-flow: column;
  }
  .Tattoo-logic {
    display: grid;
    justify-content: center;
  }
  .Tattoo-page {
    display: grid;
    grid-auto-flow: row;
  }
}

.column-sum {
  margin: 20px;
  font-size: calc(5px + 2vmin);
  width: 60%;
  /*border-style: solid;*/
  /*border-color: indianred;*/
  /*border-width: 3px;*/
}

.column-sum > p {
  margin: 0;
  text-align: right;
}

.Tattoo-page {
  /*display: grid;*/
}

.input {
  vertical-align: middle;
  zoom: 2;
}

.input-label {
}

.Tattoo-summary {
  width: 100%;
}

.Tattoo-advanced {
  margin: 20px;
  /*border-style: solid;*/
  /*border-color: #33cb45;*/
  /*border-width: 3px;*/
  flex-grow: 1;
  width: 40%;
}

.preference {
  display: flex;
  justify-content: end;
  width: 60%;
  margin: 0.5rem;
}
